import Vue from 'vue'
import Router from 'vue-router'
import Home from '@/pages/Home'
import BlogList from '@/pages/BlogList'
import RandomPair from '@/components/product/RandomPair'
import AccelerationSensor from '@/components/product/AccelerationSensor'
import puchi from '@/components/product/puchi'
import sai from '@/components/product/sai'
import RandomPrefecture from '@/components/product/RandomPrefecture'
import B1 from '@/pages/Blogs/B1'
import B2 from '@/pages/Blogs/B2'
import B3 from '@/pages/Blogs/B3'
import B4 from '@/pages/Blogs/B4'
import B5 from '@/pages/Blogs/B5'
import B6 from '@/pages/Blogs/B6'
import B7 from '@/pages/Blogs/B7'
import B8 from '@/pages/Blogs/B8'
import B9 from '@/pages/Blogs/B9'
import B10 from '@/pages/Blogs/B10'
import B11 from '@/pages/Blogs/B11'
Vue.use(Router)

export default new Router({
  // mode: 'history',
  routes: [{
    path: "*",
    redirect: Home,
  },
  {
    path: '/',
    component: Home
  },
  {
    path: '/bloglist',
    component: BlogList
  },
  {
    path: '/RandomPair',
    component: RandomPair
  },
  {
    path: '/AccelerationSensor',
    component: AccelerationSensor
  },
  {
    path: '/puchi',
    component: puchi
  },
  {
    path: '/sai',
    component: sai
  },
  {
    path: '/RandomPrefecture',
    component: RandomPrefecture
  },
  {
    path: '/B1',
    component: B1
  },
  {
    path: '/B2',
    component: B2
  },
  {
    path: '/B3',
    component: B3
  },
  {
    path: '/B4',
    component: B4
  },
  {
    path: '/B5',
    component: B5
  },
  {
    path: '/B6',
    component: B6
  },
  {
    path: '/B7',
    component: B7
  },
  {
    path: '/B8',
    component: B8
  },
  {
    path: '/B9',
    component: B9
  },
  {
    path: '/B10',
    component: B10
  },
  {
    path: '/B11',
    component: B11
  }]
})



