}
</script>

<style scoped>
.parentcontents {
  text-align: center;
}
.childcontents {
  display: inline-block;
  text-align: left;
}
article {
  width: 90%;
  display: inline-block;
  padding: 2%;
  margin-top: 0%;
  margin-bottom: 3%;
  margin-left: 3%;
  margin-right: 3%;
  background-color: rgba(255, 255, 255, 0.8);
  box-shadow: 1px 1px 1px rgba(0, 0, 0, 0.5);
  text-align: left;
}
h2 {
  font-family: "春夏秋冬ⅡB";
  color: white;
  margin: 1%;
  font-size: 25px;
}
p {
  margin: 1% 0%;
  font-size: 20px;
}
li {
  list-style: none;
  font-size: 20px;
}
</style>



