<template>
  <div>
    <h2 class="subtitle">作ったPWA</h2>
      <article>
        <ul>
          <li v-for=" product in  Products" :key="product.index">
            <router-link :to="product.to">
              <p>{{ product.title }}</p>
            </router-link>
          </li>
        </ul>
      </article>
  </div>
</template>

<script>
import ProductData from '../data/ProductData'
export default {
  mixins:[
    ProductData
  ]
}
</script>

<style scoped>
article {
  display: inline-block;
  padding: 2%;
  margin-top: 0%;
  margin-bottom: 3%;
  margin-left: 3%;
  margin-right: 3%;
  background-color: rgba(255, 255, 255, 0.8);
  box-shadow: 1px 1px 1px rgba(0, 0, 0, 0.5);
  text-align: left;
}
p {
  margin: 1% 0%;
  font-size: 20px;
}
h2 {
  font-family: "春夏秋冬ⅡB";
  color: white;
  margin: 1%;
  font-size: 25px;
}
ul {
  margin: 0;
  padding: 0;
  list-style: none;
}
li {
  margin: 3% 0%;
}
</style>



