<template>
<div>
  <div v-for="blog in Blogs.slice().reverse()" :key="blog.index">
    <BlogListTem :BlogInf='blog'></BlogListTem>
  </div>
</div>
</template>
<script>
import BlogData from '../data/BlogData'
import BlogListTem from '../components/BlogListTem'
export default {
  mixins: [
    BlogData
  ],
  components: {
    BlogListTem
  }
}
</script>

