<template>
  <footer>
    <div>
      <a href='mailto:marukome.diary@gmail.com'>
        <img :src='MailImg' alt='Mail' class='FooterImg'>
      </a>
      <a href='https://twitter.com/Ka2Robo'>
        <img :src='TwitterImg' alt='Twitter' class='FooterImg'>
      </a>
      <a href='https://github.com/okomeee'>
        <img :src='GitHubImg' alt='Github' class='FooterImg'>
      </a>
    </div>
  </footer>
</template>

<script>
export default {
  data () {
    return {
      MailImg: require('../../assets/images/freemailicon.png'),
      TwitterImg: require('../../assets/images/Twitter_Social_Icon_Circle_Color.png'),
      GitHubImg: require('../../assets/images/Octocat.png')
    }
  }
}
</script>

<style>
footer{
  margin: 5% 10%;
  text-align: right;
}
footer div{
  display: inline-block;
  padding: 1% 2%;
  background-color: rgba(255, 255, 255, 0.5);
}
.FooterImg {
  width: 20px;
  height: 20px;
}
</style>



