<template>
  <header>
    <div class="contents">
      <router-link to="/bloglist">
        <img :src='HeaderImg' alt='MyBike:Ninja250' class='image'>
      </router-link>
      <div class='SiteInf'>
        <router-link to='/'>
          <h1>まるこめ日記</h1>
        </router-link>
        <p>趣味やらなんやら書きます</p>
        <p class='UpdateDate'>最終更新：2018年7月17日(火)</p>
      </div>
    </div>
  </header>
</template>

<script>
export default {
  data () {
    return {
      HeaderImg: require('../../assets/images/Vso59x2r_400x400.jpg')
    }
  }
}
</script>

<style scoped>
header {
  margin-top: 5%;
  margin-bottom: 5%;
  background-color: rgba(255, 255, 255, 0.8);
  box-shadow: 1px 1px 1px rgba(0, 0, 0, 0.5);
}
h1 {
  font-family: "春夏秋冬ⅡB";
  margin: 0;
  font-size: 40px;
}
.contents {
  margin: 2%;
}
.SiteInf {
  display: inline-block;
  vertical-align: middle;
}
.SiteInf p {
  margin: 1% 0;
}
.UpdateDate {
  margin: 1% 0%;
  font-size: 16px;
}
.image {
  width: 80px;
  height: 80px;
  border-radius: 10%;
  margin: 1%;
  vertical-align: middle;
}
</style>



