<template>
  <div class='animation'>
    <Header></Header>
    <router-view/>
    <Footer></Footer>
    <p>(c) 2018 okomeee</p>
  </div>
</template>

<script>
import Header from './Header'
import Footer from './Footer'
export default {
  components: {
    Header,
    Footer
  }
}
</script>

<style scoped>
p{
  color: white;
}
.animation {
  animation: fadeIn 1s ease-in;
  font-family: "春夏秋冬ⅡB";
}
@keyframes fadeIn {
  0% {
    opacity: 0;
  }
  20% {
    opacity: 1;
  }
  100% {
    opacity: 1;
  }
}
</style>



