}
</script>

<style scoped>
article {
  display: inline-block;
  padding: 2%;
  margin-top: 0%;
  margin-bottom: 3%;
  margin-left: 3%;
  margin-right: 3%;
  background-color: rgba(255, 255, 255, 0.8);
  box-shadow: 1px 1px 1px rgba(0, 0, 0, 0.5);
  text-align: left;
}
p {
  margin: 1% 0%;
  font-size: 20px;
}
h2 {
  width: 100%;
  font-family: "春夏秋冬ⅡB";
