<template>
  <v-ons-page id="app">
      <div v-if=isShow>
        <Welcome></Welcome>
      </div>
      <div v-else>
        <Flame></Flame>
      </div>
  </v-ons-page>
</template>

<script>
import Loading from './components/Loading'
import Welcome from './components/Welcome'
import Test from './components/Test'
import Flame from './pages/Flame/Flame'
export default {
  components: {
    Loading,
    Welcome,
    Flame,
    Test
  },
  data: function () {
    return {
      isShow: true
    }
  },
  created: function () {
    setTimeout(() => (this.isShow = false), 2000)
  }
}
</script>
<style>
#app {
  height: 100%;
  font-family: "春夏秋冬ⅡB";
  text-align: center;
}
a {
  text-decoration: none;
  color: black;
}
@font-face {
  font-family: "春夏秋冬ⅡB";
  src: url("./assets/fonts/春夏秋冬ⅡB.ttf");
  /* src: url("./assets/fonts/春夏秋冬ⅡB.eot"); */
}
</style>



