<template>
  <v-ons-page>
    <ring-loader class='v-spinner' :color='color' :size='size'></ring-loader>
  </v-ons-page>
</template>

<script>
import RingLoader from 'vue-spinner/src/RingLoader.vue'
export default {
  components: {
    RingLoader
  },
  name: 'loading',
  data () {
    return {
      color: '#fff',
      size: '50px'
    }
  }
}
</script>

<style scoped>
.v-spinner {
  position: absolute;
  top: 20%;
  left: 45%;
}
</style>



